<template>
    <div>
        <div id="content">
          <form id="userfom" name="userfom">
            <h1>ATGO股票交易系统</h1>
            <div>用户名：<input type="text" v-focus v-model="username" id="username" name="username" placeholder="请输入用户名"/></div>
            <div><span class="sp">密码</span>：<input type="password" v-model="psw" name="password" id="password" placeholder="请输入密码"/></div>
            <div>手机号：<input type="text" v-model="phoneNum" maxlength="11" id="userPhone" placeholder="请输入手机号"/></div>
            <div class="cod">
              验证码：
              <input type="text" v-model="cod" maxlength="6" id="userCod" placeholder="请输入验证码"/><span id="getCod" v-bind:class="disabled" @click="getCod">获取验证码</span>
            </div>
            <!-- <button id="logBut" type="submit">登录</button> -->
            <button id="logBut" type="button" @click="goLogin">登录</button>
            <button id="register" type="button" @click="goRegister">注册</button>
          </form>
        </div>
        <swiper :options="swiperOption"  ref="mySwiper">
          <!-- 这部分放你要渲染的那些内容 -->
            <swiper-slide>
              <img src="@/assets/img/backgrounds/4.jpg" alt="" srcset="">
            </swiper-slide>
            <swiper-slide>
              <img src="@/assets/img/backgrounds/5.jpg" alt="" srcset="">
            </swiper-slide>
            <swiper-slide>
              <img src="@/assets/img/backgrounds/6.jpg" alt="" srcset="">
            </swiper-slide>
          </swiper>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common";
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "login",
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      username: "", //用户名
      psw: "", //密码
      phoneNum: "", //手机号
      cod: "", //验证码
      disabled: "",
      // flag:'',
      swiperOption: {
        //是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
        notNextTick: true,
        slidesPerView: "auto",
        autoplay: true,
        disableOnInteraction: false,//用户操作swiper之后，是否禁止autoplay
        effect : 'flip',//切换效果
        centeredSlides: true,
        spaceBetween: 0,
        onSlideChangeEnd: swiper => {
          //这个位置放swiper的回调方法
          this.page = swiper.realIndex + 1;
          this.index = swiper.realIndex;
        }
      }
    };
  },
  //定义这个sweiper对象
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  // 自定义指令
  directives: {
    // 获得焦点
    focus: {
      inserted: function(e) {
        e.focus();
      }
    }
  },
  methods: {
    // 
    getAuthbaseUrl(){
      console.log(domain.authbaseUrl)
      axios.get(domain.authbaseUrl)
      .then((response) => {
        console.log(response)
      })
      .catch((error) => {
        console.log(error)
        window.open(domain.authbaseUrl)
      })
    },
    // 获取验证码
    getCod: async function(e) {
      let els = e.target;
      console.log("点击获取验证码", this.phoneNum, els, typeof els,domain.logGetCod+this.phoneNum);
      if (common.isNull(this.phoneNum)) {
        common.altPrompt("手机号不能为空");
        return false;
      }
      this.disabled = "disabled";
      common.daojishi("60");
      await axios
        .get(domain.logGetCod+this.phoneNum)
        .then(function(response) {
          // 200有 400 没有
          console.log(response);
          if (response.data.httpCode == "200") {
            common.altPrompt("验证码已发送");
          } else{
            common.altPrompt("发送频率过快，请稍后重试");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 登录
    goLogin: async function() {
      //校验手机号
      let userPhoReg = /^((13[0-9])|(14[5|7])|(17[5|7|6])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/;
      //校验验证码
      let userCodReg = /^[0-9]{6}$/;
      console.log(this.username, this.psw, this.phoneNum, this.cod);
      if (common.isNull(this.username)) {
        common.altPrompt('用户名不能为空')
        return false
      } else if(common.isNull(this.psw)){
        common.altPrompt('密码不能为空')
        return false
      }else if(common.isNull(this.phoneNum)){
        common.altPrompt("手机号不能为空")
        return false;
      }else if (!userPhoReg.test(this.phoneNum)) {
        common.altPrompt("手机号格式错误");
        return false;
      }else if (common.isNull(this.cod)) {
        common.altPrompt("验证码不能为空");
        return false;
      }else if (!userCodReg.test(this.cod)) {
        common.altPrompt("验证码格式");
        return false;
      }

      //登录
      console.log(domain.logLogin);
      await axios
        .post(domain.logLogin,
          {
            username:this.username,
            password:this.psw,
            phone:this.phoneNum,
            smsCode:this.cod
          })
        .then(
          function(response) {
            console.log(response);
            if (response.data.httpCode == '500') {
              common.altPrompt(response.data.errorMsg)
              return false
            } else if (response.data.httpCode == '200'){
              // 存储用户名，下单页面获取此处用户名，然后展示产品
              let datas = response.data.responseEntity;
            // 存入local
              common.encrypt('token', datas.token);
              common.encrypt('wsUserID', datas.wsUser.userid);
              common.encrypt('wsCustomerID', datas.wsUser.customerId);
              common.encrypt('wsUserName', datas.wsUser.username);
              common.encrypt('wsUserRole',datas.wsUser.role);
              // 存入session
  // 								let token = datas.token;
  // 								let userID = datas.wsUser.userid;
  // 								let customerID = datas.wsUser.customerId;
  // 								window.sessionStorage.setItem('token', token)
  // 								window.sessionStorage.setItem('userID', userID)
  // 								window.sessionStorage.setItem('customerID', customerID)
              this.$message({
                type: 'success',
                message: '登陆成功',
              })
              if (datas.wsUser.role == "trader") {
                // 下单页面
                this.$router.push({
                  name: "tabsEntrust",
                  params: { token: datas.token }
                });
              }  else if (datas.wsUser.role == "admin") {
                // 管理员页面
                this.$router.push({
                  name:'tabsSee',
                  params:{token:datas.token}
                })
              }
            }
          }.bind(this)
        )
        .catch(function(error) {
          console.log(error);
        });
    },
    // 注册
    goRegister(){
      console.log('注册')
      this.$router.push({
        name:'register',
      })
    },
  },
  //生命周期，钩子函数
  // 1.beforeCreate,在实例初始化之后，数据观测(data observer) 和 event/watcher 事件配置之前被调用。
  beforeCreate: function() {
    console.log("1.实例创建之前执行本钩子");
  },
  // 2.created,实例已经创建完成之后被调用。在这一步，实例已完成以下的配置：数据观测(data observer)，属性和方法的运算， watch/event 事件回调。然而，挂载阶段还没开始，$el 属性目前不可见。
  created: function() {
    console.log("2.实例已经创建完成调用本钩子");
  },
  // 3.beforeMount,再挂载开始之前被调用：相关的render函数首次被调用
  beforeMount: function() {
    console.log("3.实例挂载之前执行本钩子");
  },
  // 4.mounted，el被新创建的vm.$el替换，并挂载到实例上去之后调用该钩子,此钩子函数一般会做一些ajax请求获取数据进行数据初始化,mounted在整个实例中只执行一次
  mounted: function() {
    console.log("4.el被新创建的$el替换，并挂载到实例上去之后调用本钩子");
    this.getAuthbaseUrl();
    this.swiper.slideTo(0, 0, false);
  },
  // 5.beforeUpdate，数据更新时调用，发生在虚拟DOM重新渲染和打补丁之前。可以在这个钩子中进一步更改状态，这不会触发附加的重渲染过程
  beforeUpdate: function() {
    console.log("5.数据更新时调用该钩子，发生在虚拟DOM重新渲染和打补丁之前");
  },
  // 6.updated，由于数据更改导致的虚拟DOM重新渲染和打补丁，在这之后会调用该钩子.当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。然而在大多数情况下，你应该避免在此期间更改状态，因为这可能会导致更新无限循环。该钩子在服务器端渲染期间不被调用。
  updated: function() {
    console.log("6.数据更新后调用该钩子，该钩子被调用时，组件DOM已经更新");
  },
  // 7.beforeDestroy，实例销毁之前调用。在这一步，实例仍然完全可用
  beforeDestroy: function() {
    console.log("7.实例销毁之前调用本钩子");
  },
  // 8.destroyed，vue实例销毁后调用。调用后，vue实例指示的所有东西都会解绑定，所有的事件监听器会被移除，所有的子实例也会被销毁。该钩子在服务器端渲染期间不被调用
  destroyed: function() {
    console.log("8.实例销毁后执行本钩子");
  }
};
</script>

<style>
#app{
      width: 100%;
    height: 100%;
}
#content {
  z-index: 100;
}
img {
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  z-index: 1;
}
</style>



